import Vue from 'vue'
import App from './App.vue'


Vue.config.productionTip = false

//vue-router
import VueRouter from 'vue-router'
Vue.use(VueRouter)
//vuex
import Vuex from 'vuex'
Vue.use(Vuex)
//axios
import axios from 'axios'
Vue.prototype.$http = axios;
//mintui
import MintUI from 'mint-ui'
Vue.use(MintUI);
//vue-lazy
import VueLazyload from 'vue-lazyload'
Vue.use(VueLazyload, {
	preLoad: 1.3,
  loading: 'http://img1.cheshi-img.com/misc/gaojiacheng/201806/5b1f84d7a1f7f.gif',
  listenEvents: [ 'scroll' ]
})
// 1.  () 
//  import 
import Home from "./containers/Home.vue";
import Search from "./containers/Search.vue";
import Detail from "./containers/Detail.vue";
import Login from "./containers/Login.vue";
import Square from "./containers/Square.vue";


//
import Club from "./containers/HomeClub.vue";
import Rank from "./containers/HomeRank.vue";
import Forum from "./containers/HomeForum.vue";

// 2. 

const routes = [{
		path: '/',
		redirect: '/home/forum'
	},
	{
		path: '/home',
		name: 'home',
		component: Home,
		children: [{
				path: 'club',
				component: Club,
				name: 'club'
			},
			{
				path: 'rank',
				component: Rank,
				name: 'rank'
			},
			{
				path: 'forum',
				component: Forum,
				name: 'forum'
			}
		]
	},
	{
		path: '/search',
		component: Search,
		name: 'search'
	},
	{
		path: '/detail',
		component: Detail,
		name: 'detail'
	},
	{
		path: '/square',
		component: Square
	},
	{
		path: '/login',
		component: Login
	}

]

// 3.  router  `routes` 
// , 
const router = new VueRouter({
	routes,
	scrollBehavior(to, from, savedPosition) { //
		return {
			x: 0,
			y: 0
		}
	}
})

const store = new Vuex.Store({
	state: {
		isnavShow: 0,
		clubCar:{
			showClubCar:false,
			clubPath:"http://120.78.219.201/m/api/travel/"
		},
		imgshowObj:{
			isimgshow:false,
			isimgshowArr:[],
			imgshowIndex:0
		}
	},
	// 
	mutations: {
		editClubCar(state, data) {
			state.clubCar = data
		},
		editimgshowObj(state, data) {
			state.imgshowObj = data
		}
	},
	// actions   @xxx 
	actions: {
		setClubCar(context, data) {
			context.commit('editClubCar', data)
		},
		setimgshowObj(context, data) {
			context.commit('editimgshowObj', data)
		},
	},
	getters: {//vue
		getClubCar: state => {
			return state.clubCar;
		},
		getClubCarPath:state => {
			return state.clubCar.clubPath;
		},
		getimgshowObj:state => {
			return state.imgshowObj;
		}
	}
})




new Vue({
	router,
	store,
	render: h => h(App),
}).$mount('#app')